<template>
  <div class="iv-container">
    <!--流程步骤条-->
    <div class="step">
      <recommend-step :step="stepProcess" :steps="steps"></recommend-step>
    </div>
    <!--业务组件渲染-->
    <router-view/>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const RecommendStep = () => import('@/components/RecommendStep')
export default {
  name: 'modify',
  components: {RecommendStep},
  computed: mapState({
    stepProcess: state => state.fleet.stepProcess
  }),
  data () {
    return {
      steps: [
        {id: '1', title: '基本信息录入', icon: 'iconfont icon-ziyuan1', path: '/admin/fleet/declare/modify/baseInfo'},
        {id: '2', title: '风险信息录入', icon: 'iconfont icon-ziyuan2', path: '/admin/fleet/declare/modify/riskInfo'},
        {id: '3', title: '车队申报配费', icon: 'iconfont icon-ziyuan3', path: '/admin/fleet/declare/modify/configProtocol'},
        {id: '4', title: '预算对比分析', icon: 'iconfont icon-ziyuan4', path: '/admin/fleet/declare/modify/collect'}
      ]
    }
  },
  methods: {
  }
}
</script>

<style scoped>
</style>



